import React, { useContext, useState } from 'react';

import styles from './Cart.module.css';
import Modal from '../UI/Modal';
import CartItem from './CartItem';
import CartContext from '../../store/cart-context';
import Checkout from './Checkout';
import classes from './Cart.module.css'

const Cart = props => {
  const [showCheckout, setShowCheckout] = useState(false);
  const [issubmitting,setissubmitting] = useState(false);
  const [didsubmit,setdidsubmit] = useState(false);

  const cartCtx = useContext(CartContext);
  const totalAmount = `Rs. ${cartCtx.totalAmount.toFixed(2)}`;
  const hasItem = cartCtx.items.length > 0;

  const cartItemRemoveHandler = id => {
    cartCtx.removeItem(id);
  };

  const cartItemIncreaseHandler = item => {
    cartCtx.addItem({ ...item, amount: 1 });
  };

  const checkoutHandler = () => {
    setShowCheckout(true);
  };

  const submitOrderHandler = async (userData) => {
    setissubmitting(true);
    await fetch(
      'https://react-http-dcabd-default-rtdb.firebaseio.com/orders.json',
      {
        method: 'POST',
        body: JSON.stringify({
          user: userData,
          orderedItems: cartCtx.items,
        }),
      }
    );
    setissubmitting(false);
    setdidsubmit(true);
  };

  const cartItems = (
    <ul className={styles['cart-items']}>
      {cartCtx.items.map(item => (
        <CartItem
          key={item.id}
          name={item.name}
          amount={item.amount}
          price={item.price}
          onRemove={cartItemRemoveHandler.bind(null, item.id)}
          onAdd={cartItemIncreaseHandler.bind(null, item)}
        />
      ))}
    </ul>
  );

  const modalAction = (
    <div className={styles.actions}>
      <button className={styles['button--alt']} onClick={props.onClose}>
        Close
      </button>
      {hasItem && (
        <button className={styles.button} onClick={checkoutHandler}>
          Order
        </button>
      )}
    </div>
  );
  const CartModalcontent = (
    <React.Fragment>
    {cartItems}
      <div className={styles.total}>
        <span>Total Amount</span>
        <span>{totalAmount}</span>
      </div>

      {showCheckout && (
        <Checkout onConfirm={submitOrderHandler} onCancel={props.onClose} />
      )}
      {!showCheckout && modalAction}
      </React.Fragment>
  )
  const issubmittingmodalcontent = <p>Sending order data...</p>
  const didsubmitmodalcontent = 
  <React.Fragment>
    <p>Succesfully ordered...</p>
    <div className={classes.actions}>
      <button className={classes.button} onClick={props.onClose}>
        Close
      </button>
      </div>
    </React.Fragment>
  return (
    <Modal onClose={props.onClose}>
      {!issubmitting && !didsubmit && CartModalcontent}
      {issubmitting && issubmittingmodalcontent}
      {!issubmitting && didsubmit && didsubmitmodalcontent}
    </Modal>
  );
};

export default Cart;